.tb_w_e_image, .tb_w_e_video{
    height: 100% !important;
    width: 100% !important;
    position: absolute;
    left: 0;
    top: 0;
    margin: 0;
    z-index: 1;
    overflow: hidden;
    display: block;
    object-fit: cover;
    object-position: center;
    background-color: #000;
}
.tb_w_e_video{
    background-color: #000;
}

.tb_w_e_video video,
.tb_w_e_video iframe{
    object-fit: cover;
    object-position: center;
}

.tb_w_e_video > div {
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_w_e_contant_wrapper {
    padding: 15px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
}

.tb_w_e_content_wrapper__ {
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    height: 100%;
    flex-grow: 1;
}

.tb_w_e_content_review__{
    margin-bottom: 15px;
}

.tb_w_e_rating__ .tb__rating__ {
    padding: 10px 0;
}

.tb_w_e_author_wrapper {
    margin-bottom: 10px;
}

.tb_w_e_author {
    margin-bottom: 15px;
}

.tb_w_e_author_profile {
    height: 50px;
    width: 50px;
    border-radius: 50%;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    display: flex;
    flex-shrink: 0;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 15px;
    margin-top: -38px;
    position: relative;
    z-index: 1;
    overflow: hidden;
    border: 2px solid #fff;
    background-color: #f7f7f7;
    box-shadow: 0 1px 2px rgba(0, 0, 0, 0.4);
}

.tb_w_e_author_wrapper_center .tb_w_e_author_profile {
    margin-top: 15px;
}

.tb_w_e_author_profile__{
    height: 100% !important;
    width: 100% !important;
    margin: 0 !important;
    object-fit: cover;
    object-position: center;
}

.tb_w_e_text_post .tb_w_e_author_profile {
    margin-top: 0px;
}

.tb_w_e_authorname {
    font-size: 14px;
    font-weight:  var(--tb-author-font-weight);
    margin-bottom: 4px;
    white-space: nowrap;
    display: block;
    overflow: hidden;
    text-overflow: ellipsis;
    text-align: center;
    line-height: normal;
}


.tb_w_e_authorname {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
}

.tb_w_e_username,
.tb_w_e_time {
    font-family: var(--tb-author-font);
    color: var(--tb-author-color);
    font-style:  var(--tb-author-font-style);
}

.tb_w_e_seprator {
    background-color: var(--tb-author-color);
}

.tb_w_e_content,
.tb_w_e_rating_content {
    font-family: var(--tb-font);
    color: var(--tb-color);
    font-size: var(--tb-font-size);
    line-height: var(--tb-line-height);
    font-weight: var(--tb-font-weight);
    font-style: var(--tb-font-style);
    width: 100%;

}

.tb_w_e_content_wrapper__{
    position: relative;
}

.tb_w_e_content_hidden{
    position: absolute;
    left: 0;
    width: 100%;
    top: 0;
    opacity: 0 !important;
    visibility: hidden !important;
}

.tb_w_e_author_info {
    overflow: hidden;
    margin-bottom: 15px;
}

.tb_w_e_post_info {
    display: flex;
    align-items: center;
    width: 100%;
    justify-content: center;
}

.tb_w_e_seprator {
    height: 4px;
    width: 4px;
    min-width: 4px;
    border-radius: 50%;
    margin: 0 6px;
    opacity: 0.8;
    display: flex;
    flex-shrink: 1;
}

.tb_w_e_username, .tb_w_e_time {
    font-size: 10px;
    font-weight: 500;
    opacity: 0.8;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
    line-height: normal;
}

.tb_w_e_username{
    max-width: 65%;
}

.tb_w_e_time{
    max-width: 35%;
}

.tb_w_e_social_ {
    font-size: 26px;
    text-align: center;
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
}

.tb_w_e_social_ico{
    font-size: 24px;
    color: var(--tb-icon-color);
}

.tb_w_e_post_in:hover .tb_share_wrapper {
    opacity: 1;
    visibility: visible;
}

.tb_w_e_social_review_{
    margin-top: 15px;
}

