//     overflow: hidden;
//     text-overflow: ellipsis;
//     display: -webkit-box;
//     -webkit-box-orient: vertical;
// }

.tb_w_e_content .tb_text_links_{
    color: inherit !important;
    text-decoration: underline !important;
    display: inline;
}

.tb_w_e_video_ico, .tb_w_e_audio_ico {
    position: absolute;
    right: 12px;
    top: 12px;
    z-index: 2;
    color: #fff;
    filter: drop-shadow(2px 2px 2px rgba(0,0,0,.2));
}
.tb_w_e_video_ico{
    font-size: 12px;
}
.tb_w_e_audio_ico{
    font-size: 16px;
}

.tb_w_e_onsite_rating__ {
    margin-bottom: 15px;
}
.tb_w_e_onsite_rating__ .tb__rating__{
    padding: 0;
}

.tb_w_e_youtube_ico {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    margin: 0 auto;
    text-align: center;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 34px;
}

.tb_w_e_post_cta {
    display: block;
    text-align: center;
    margin-bottom: 10px;
}

.tb_w_e_post_cta_btn {
    font-family: var(--tb-font);
    padding: var(--tb-cta-padding);
    color: var(--tb-cta-color);
    background-color:  var(--tb-cta-bg-color);
    border-radius: var(--tb-cta-border-radius);
    font-weight: var(--tb-cta-font-weight);
    border-color: var(--tb-cta-border-color);
    transition: var(--tb-cta-transition);
    min-height: var(--tb-cta-height);
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 14px;
    line-height: normal;
}

.tb_w_e_post_cta_btn:hover {
    color: var(--tb-cta-color-hover);
    background-color: var(--tb-cta-bg-color-hover);
    border-color: var(--tb-cta-border-color-hover);
}

.tb_w_e_rating__{
    display: flex;
    align-items: center;
    justify-content: center;
}

.tb_w_e_rating_content, .tb_w_e_content {
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-box-orient: vertical;
}

.tb_w_e_fixed_height .tb_w_e_content {
    -webkit-line-clamp: 3;
}

.tb_w_e_text_decoration::before{
    content: "\f10d";
    font-family: 'WebSocialFonts';
    margin-right: 12px;
}

.tb_w_e_text_decoration::after{
    content: "\f10e";
    font-family: 'WebSocialFonts';
    margin-left: 12px;
}

.tb_w_e_post_in:hover .tb__media_ico_ .tb__icon,
.tb_w_e_post_in:hover .tb__media_ico_ .tb__icon{
    opacity: 0;
    transform: scaleX(.4) scaleY(.4) translateZ(0);
}

.tb_w_e_arrow_wrapper_{
    width: 100%;
}

.tb_w_e_arrow {
    position: absolute;
    top: 30%;
    background-color: #fff;
    border-radius: 3px;
    height: 40px;
    width: 40px;
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    color: #000;
    font-size: 16px;
    opacity: 0.75;
    z-index: 9;
    cursor: pointer;
    -webkit-transform: translateY(0) !important;
    transform: translateY(0) !important;
}

.tb_w_e_fixed_height .tb_w_e_arrow,
.tb_w_e_fixed_height_te .tb_w_e_arrow {
    top: calc(50% - 20px);
}

.tb_w_e_arrow:hover{
    opacity: 1;
}

.tb_w_e_arrow_left__{
    left: 5px;
    right: auto;
    box-shadow: 3px 0px 3px rgba(0, 0, 0, 0.15);
}

.tb_w_e_arrow_right__{
    right: 5px;
    left: auto;
    box-shadow: -3px 0px 3px rgba(0, 0, 0, 0.15);
}
